.video-single-card {
    &--poster {
        position: relative;
        overflow: hidden;
        transform: translateX(-15px);
    }

    &--icon-new {
        position: absolute;
        top: 2px;
        right: 2px;
        z-index: 1010;

        svg {
            filter: drop-shadow(1px 1px 2px rgba(#111111, 0.7));
        }
    }

    &--icon-format {
        position: absolute;
        top: 10px;
        left: 10px;
        z-index: 1010;

        svg {
            filter: drop-shadow(1px 1px 2px rgba(#111111, 0.7));
        }
    }
}
