.video-single {
    min-height: 100vh;
    overflow: hidden;
    position: relative;
    
    &--poster {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        transform: scale(1.1);
        filter: blur(20px) grayscale(0.5);
    }
}