.video-card-list {
    &--poster {
        position: relative;
    }

    &--icon-format {
        position: absolute;
        top: 10px;
        left: 10px;
        z-index: 1010;

        svg {
            filter: drop-shadow( 1px 1px 2px rgba(#111111, .7));
        }
    }

    &--icon-new {
        position: absolute;
        top: 2px;
        right: 2px;
        z-index: 1010;

        svg {
            filter: drop-shadow( 1px 1px 2px rgba(#111111, .7));
        }
    }

    &,
    &--icon-new,
    &--icon-format,
    .card-header,
    .list-group {
        transition: all 0.3s;
    }

    &:hover {
        text-decoration: unset;
        margin-left: -5px;
        margin-right: -5px;

        .card-header,
        .list-group,
        .video-card-list--icon-new,
        .video-card-list--icon-format {
            margin-left: 5px;
            margin-right: 5px
        }
    }

    .card-img-top {
        width: 100%;
        height: 250px;
        object-fit: cover;
    }
}
