.swiper-container {
    width: 100%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
}

.swiper-slide {
    background-size: cover;
    background-position: center;
}

.swiper-button {
    background-color: transparent;
    border: none;
    padding: 0;
}

.gallery-top {
    height: 400px;
    width: 100%;
}

.gallery-thumbs {
    height: 120px;
    box-sizing: border-box;
    padding: 10px 0;

    .swiper-slide {
        width: 25%;
        height: 100%;
        opacity: 0.4;
        cursor: pointer;
    }

    .swiper-slide-thumb-active {
        opacity: 1;
    }
}